.sendForm {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: start;
    padding: 15px;
}

.textarea {
    width: 976px;
    height: 40px;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    border: none;
    padding: 10px 20px 10px 20px;
    resize: none;

    font-weight: 400;
    font-size: 14px;
    line-height: 17px;

    color: rgba(0, 0, 0, 0.6);

    opacity: 0.9;
}

.button {
    width: 120px;
    height: 40px;

    background: #0066CC;
    border-radius: 20px;
    border: none;

    font-size: 16px;
    line-height: 20px;
    /* identical to box height */

    color: #FFFFFF;
}

